import { useEffect, useState } from "react";
import { extractToken } from "./Login/extractToken";
import { Login } from "./Login/Login";
import { MangaGrid } from "./Manga/MangaGrid";
import { ApolloProvider } from "@apollo/client";
import client from "./Common/apolloClient";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faMastodon } from "@fortawesome/free-brands-svg-icons";

import 'tailwindcss/tailwind.css';
import { Footer } from "./Common/Footer";

const App = () => {
  const [token, setToken] = useState<string | null>(localStorage.getItem('anilist_token'));

  useEffect(() => {
    const tokenFromUrl = extractToken();
    if (tokenFromUrl) {
      localStorage.setItem('anilist_token', tokenFromUrl); // Token im LocalStorage speichern
      setToken(tokenFromUrl);
      window.history.replaceState({}, document.title, window.location.pathname); // Entfernen des Tokens aus der URL und Beibehaltung des Pfads
    }
  }, []);

  return (
    <ApolloProvider client={client}>
      <main className="app container pb-4 pl-4 pr-4 min-h-screen">
        <header className="App-header bg-secondary text-white p-6 mb-4 rounded-b-lg flex items-center justify-between">
          <h1 className="text-4xl">Anilist.co Companion</h1>
          <a href="https://tech.lgbt/@owlex" target="_blank" rel="noopener noreferrer" className="text-white">
            <FontAwesomeIcon icon={faMastodon} /> Feedback
          </a>
        </header>
        {token ? <MangaGrid /> : <Login />}
      </main>
      <Footer />
    </ApolloProvider>
  );
};

export default App;

