// src/components/MangaGrid.tsx

import React, { useEffect, useState } from 'react';
import { Manga } from './Interfaces';
import { useQuery } from '@apollo/client';
import { GET_MEDIA_LIST } from './queries';
import AnilistLogo from './AnilistLogo';
import { GET_USER_ID } from '../Login/queries';

interface WithScore {
  score: number
  [key: string]: any
}


export const MangaGrid: React.FC<{ className?: string }> = () => {
  const { loading: userIdLoading, data: userIdData } = useQuery(GET_USER_ID);
  const { loading: mediaListLoading, data: mediaListData } = useQuery(GET_MEDIA_LIST, {
    variables: { userId: userIdData?.Viewer?.id },
    skip: !userIdData?.Viewer?.id,
  });

  const [mangas, setMangas] = useState<Manga[]>([]);

  useEffect(() => {
    if (!mediaListData) return
    let mediaList: Array<{ id: string, score: number, title: { userPreferred: string } }> =
      mediaListData.MediaListCollection.lists
        .flatMap((mediaListGroup: any[]) => mediaListGroup.entries)
        .map((entry: { media: { id: string } }) => entry.media)

    let recommendationDictionary = mediaListData.MediaListCollection.lists
      .flatMap((mediaListGroup: any[]) => mediaListGroup.entries)
      .flatMap((mediaList: {
        media: {
          [x: string]: any; recommendations: { edges: any; };
        };
      }) => mediaList.media.recommendations.edges.map((edge: any) => ({ ...edge, source: mediaList.media.id })))
      .filter((en: { node: { mediaRecommendation: null; } | null; } | null) => !(en == null || en.node == null || en.node.mediaRecommendation == null))
      .reduce((pre: { [x: string]: { id: any; title: any; imageUrl: any; score: number; }; }, curr: { source: number, node: { mediaRecommendation: { id: string | number; title: { userPreferred: any; }; coverImage: { large: any; }; }; }; }) => {
        if (pre[curr.node.mediaRecommendation.id]) {
          pre[curr.node.mediaRecommendation.id].score =
            pre[curr.node.mediaRecommendation.id].score + 1
        } else {
          pre[curr.node.mediaRecommendation.id] = {
            id: curr.node.mediaRecommendation.id,
            title: curr.node.mediaRecommendation.title.userPreferred,
            imageUrl: curr.node.mediaRecommendation.coverImage.large,
            score: 1
          }
        }
        return pre
      }, {})

    for (let media of mediaList) {
      delete recommendationDictionary[media.id]
    }

    let mappedMangas = Object.values(recommendationDictionary)
      .sort((a, b) => (b as WithScore).score - (a as WithScore).score)
      .map((el: any) => ({
        id: el.id,
        coverImageUrl: el.imageUrl,
        title: el.title,
        score: el.score
      } as Manga))

    setMangas(mappedMangas.slice(0,100))
  }, [mediaListLoading]); // eslint-disable-line react-hooks/exhaustive-deps

  if (userIdLoading || mediaListLoading) {
  // if (true) {
    return <div>Loading...</div>;
  }

  return (
    <div className="grid grid-cols-1 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-6 gap-4 mx-auto" style={{ display: 'grid' }}>
      {mangas.map((manga) => (
        <div key={manga.id}
          className="bg-secondary rounded-lg shadow-md flex flex-col hover:bg-accent-300">
          <a href={`http://anilist.co/manga/${manga.id}`} target="_blank" rel="noopener noreferrer">
            <img src={manga.coverImageUrl}
              alt={manga.title}
              className="w-full h-48 object-contain"
              loading="lazy" />
          </a>
          <div className="p-4 flex-grow">
            <h3 className={`text-lg text-white font-semibold ${manga.title.length > 20 ? 'text-sm' : ''}`}>{manga.title}</h3>
          </div>
          <a
            href={`http://anilist.co/manga/${manga.id}`}
            target="_blank"
            rel="noopener noreferrer"
            className="inline-flex items-center px-4 py-2 text-sm font-medium text-white bg-primary-500 rounded-md hover:bg-primary-800"
          >
            <AnilistLogo className="w-4 h-4 mr-2" /> anilist.co
          </a>
        </div>
      ))}
    </div>
  );
};
