import { gql } from '@apollo/client';

export const GET_MEDIA_LIST = gql`
query ($userId: Int) {
    MediaListCollection(userId: $userId, type: MANGA) {
      lists {
        name
        isCustomList
        isCompletedList: isSplitCompletedList
        entries {
          ...mediaListEntry
        }
      }
    }
  }
  
  fragment mediaListEntry on MediaList {
    id
    mediaId
    score
    media {
      id
      title {
        userPreferred
      }
      recommendations {
        edges {
          node {
            mediaRecommendation {
              id
              coverImage {
                large
              }
              title {
                userPreferred
              }
            }
            rating
          }
        }
      }
      coverImage {
        large
      }
    }
  }  
`